/* Styling for the App */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background: #f4f4f4;
  color: #333;
  line-height: 1.6;
}

/* Header Styling */
.header {
  text-align: center;
  padding: 40px 0;
  background: #007bff;
  color: white;
  font-size: 32px;
  font-weight: bold;
}

/* Courses Container */
.courses-container {
  display: flex;
  justify-content: center;
  gap: 30px;
  padding: 40px;
  flex-wrap: wrap;
}

/* Individual Course Cards */
.course-card {
  width: 400px;
  background: white;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}

.course-card:hover {
  transform: translateY(-10px);
}

/* Course Image */
.course-img {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

/* Course Content */
.course-content {
  padding: 20px;
  text-align: center;
}

.course-content h3 {
  font-size: 24px;
  color: #007bff;
}

.course-content p {
  font-size: 16px;
  color: #555;
  margin: 15px 0;
}

/* Button Styling */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  text-decoration: none;
  color: white;
  background: #28a745;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background 0.3s;
}

.btn:hover {
  background: #218838;
}

.btn svg {
  font-size: 16px;
}
